import React from 'react';
import ReactDOM from 'react-dom'
import {urls, services} from 'api';
import {Table, Input, Button, Icon} from 'antd';
import Highlighter from 'react-highlight-words';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import './message.less'

export default class MessageBoard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      personalInfo: {},
      searchText: '',
    }
  }
  componentDidMount() {
      // mockget
      services.get(urls.personalInfo, {}, this.getPersonInfo);
      document.documentElement.scrollTop = document.body.scrollTop = 0;
      const tableCon = ReactDOM.findDOMNode(this.refs['tables'])// reactdom.finddomnode()DOM
      const table = tableCon.querySelector('table')
      table.setAttribute('id', 'table-to-xls')// tableid
 }

  getPersonInfo = (data) => {
      console.log('getPersonInfo', data);
    }

    getColumnSearchProps = dataIndex => ({
      filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <div style={{ padding: 8 }}>
          <Input
            ref={node => {
              this.searchInput = node;
            }}
            placeholder={`Search ${dataIndex}`}
            value={selectedKeys[0]}
            onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
            onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
            style={{ width: 188, marginBottom: 8, display: 'block' }}
          />
          <Button
            type='primary'
            onClick={() => this.handleSearch(selectedKeys, confirm)}
            icon='search'
            size='small'
            style={{ width: 90, marginRight: 8 }}
          >
            Search
          </Button>
          <Button onClick={() => this.handleReset(clearFilters)} size='small' style={{ width: 90 }}>
            Reset
          </Button>
        </div>
      ),
      filterIcon: filtered => (
        <Icon type='search' style={{ color: filtered ? '#1890ff' : undefined }} />
      ),
      onFilter: (value, record) =>
        record[dataIndex]
          .toString()
          .toLowerCase()
          .includes(value.toLowerCase()),
      onFilterDropdownVisibleChange: visible => {
        if (visible) {
          setTimeout(() => this.searchInput.select());
        }
      },
      render: text => (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ),
    });

    handleSearch = (selectedKeys, confirm) => {
      confirm();
      this.setState({ searchText: selectedKeys[0] });
    };

    handleReset = clearFilters => {
      clearFilters();
      this.setState({ searchText: '' });
    };

  render() {
    const columns = [
      {
        title: '',
        dataIndex: 'name',
        key: 'name',
        width: '10%',
        ...this.getColumnSearchProps('name'),
      },
      {
        title: '',
        dataIndex: 'phoneNum',
        key: 'phoneNum',
        width: '10%',
        ...this.getColumnSearchProps('phoneNum'),
      },
      {
        title: '',
        dataIndex: 'email',
        width: '10%',
        key: 'email',
        ...this.getColumnSearchProps('email'),
      },
      {
        title: '',
        dataIndex: 'content',
        key: 'content',
        ...this.getColumnSearchProps('content'),
      },
    ];
    const data = [
      {
        key: '1',
        name: 'John Brown',
        phoneNum: 15012345678,
        email: '1229284424@qq.com',
        content: ' v  v  v '
      },
      {
        key: '2',
        name: 'Joe Black',
        phoneNum: 15112345678,
        email: '1229284424@qq.com',
        content: ' v  v  v '
      },
      {
        key: '3',
        name: 'Jim Green',
        phoneNum: 15212345678,
        email: '1229284424@qq.com',
        content: ' v  v  v '
      },
      {
        key: '4',
        name: 'Jim Red',
        phoneNum: 15312345678,
        email: '1229284424@qq.com',
        content: ''
      },
    ];
    return (
      <div className='message'>
        <div className='h1Css'>
          <h1 ></h1>
        </div>
        <div className='title'>
          <ReactHTMLTableToExcel
            className='downloadButton'
            table='table-to-xls'
            filename=''
            sheet=''
            buttonText='' />
        </div>
        <Table columns={columns} dataSource={data} ref='tables' />;
      </div>
    )
  }
}
